<!-- style="position: relative;top: -84px;" -->
    <div class="personal-container" @click="headerUserDropboxhide" style="position: relative;top: -20px;">
        <!-- <div class="personal-header-box2">
            <div class="personal-header">
                <div class="header-logo">
                    <img src="../../images/personal-logo2.png" alt="" @click="goToHome('homepage')">
                    <img src="../../images/personal_line2.png" alt="">
                    <span>XPE 智能办公平台</span>
                </div>
                <div class="header-user">
                    <div @click.stop="changeHeaderUserDropboxShow"><img @error="getimg" :src="userUrl">
                        <span>{{userName}}</span></div>
                    <div class="header-user-dropbox" :class="{'header-user-dropbox-show':headerUserDropboxShow}">
