                </div>
            </div>
        </div> -->
        <div class="" >
            <div class="title_box">
                <div class=" title_box_line title_box_line1"></div>
                <div class="title_box_line title_box_line2"></div>
            </div>
            <div class="editor_container_box" :class="{'editor_container_box_hide':isclick}">
                <div class="editor_container">
                    <div class="editor_container_title">
                        <span>智能文书</span>
                        <div class="editor_btn_box">
                          <span @click="getTempPreview(1)">预览</span>
                          <span @click="getTempPreview(2)">下载</span>
                          <span @click="getTempPreview(3)">保存</span>
                        </div>
                    </div>
                    <!-- <editor id="tinymce" v-model="tinymceHtml" :init="editorInit" class="editor_container_content" v-if="tinymceHtml!=null"></editor> -->
                    <report-editor-comment v-model= "tinymceHtml" :url= "Url" class="editor_container_content" v-if="tinymceHtml!=null" @on-upload-success= "onEditorUploadComplete" ></report-editor-comment>
                </div>
            </div>
            
            <div class="operate_all_box" :class="{'operate_all_box_click':isclick}">
