                    <div class="showAndHidenBtn" @click="toClick">
                        <img src="../../images/icon_right_arrow.png" alt="">
                    </div>
                    <span>合同库</span>
                </div>
                <div class="operate_content_box">
                    
                    <div class="operate_content_line clear" v-if="!onDeatail">
                        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true'  @change="getData(1)" >
                            <i slot="prefix" class="el-input__icon el-icon-search "></i>
                        </el-input>
                    </div>
                    
                    <div class="operate_content_line_box" v-if="!onDeatail">
                        <div class="operate_content_line" v-for="(item,index) of serarchData" :key="index" v-if="total!=0">
                            <div class="operate_content_line_item">
                                <h3 class="item_title">{{item.pdf_name}}</h3>
                                <p class="item_content" >{{item.overview}}</p>
                                <div class="item_operate_box">
                                    <button class="item_operate_btn" @click="toOpenDoc(item.content)">使用该文档</button>
                                </div>
                            </div>
                        </div>
                        <div class=" table-nodata-editor" v-if="total==0">
                          <img src="../../images/table_nodata.png"> 没有对应数据
                        </div>
                    </div>
                    
                    <div class="operate_content_line" v-if="!onDeatail">
                        <div class="pagination-normal" v-if="total!=0">
                            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
                            </el-pagination>
                        </div>
                    </div>
                    
                    <div class="detail_box_nav"  v-if="onDeatail">
                        <span @click="toBack">返回</span>
                    </div>
                    <div id="detail_box" v-if="onDeatail"  @scroll="ishow=false" >
                        <div class="hover_box" v-if="ishow" :style="{'width':clientWidth+'px','height':clientHeight+'px','top':offsetTop+'px'}"> <button @click="toInsert">插入到文档</button></div>
                        <div class="detail_box_info" v-html="deatailInfo" @mouseover="fun"></div>
                    </div>
                     
                </div>
            </div>
