</template>

<script>
import {
  apiAutoLogin,
  getIntelligentDoc,
  tempInfo,
  tempPreview
} from "../../config/api";
import api from "../../config/url.js";
import { mapState, mapMutations } from "vuex";
import {
  setStore,
  getStore,
  removeStore,
  dialogConfirm
} from "../../config/mUtils";
import reportEditorComment from './reportEditorComment'
export default {
  data() {
    return {
      firstClickId: "",
      secondClickId: "",
      thirdClickId: "",
      isChildClick: "", //左边二级导航
      defaultOpeneds: ["/home/administrativeManageHomepage"], //左侧导航默认打开项
      defaultActive: "", //左侧导航默认选中项
      leftNavList: "", //左侧导航栏数据
      headerUserDropboxShow: false, //头部用户信息下拉框
      userImgurl: "",
      selectId: null,
      selectName: "",
      tipspanShow: false,
      fulltipspan: false,


      tinymceHtml: null,
      keyWord: "合同",
      total: 0,
      currentPage: 1,
      pageSize: 10,
      isclick: false,
      serarchData: [],
      onDeatail: false,
      deatailInfo: "",
      insertHtml: "",
      clientWidth: "",
      clientHeight: "",
      ishow: false,
      offsetTop: 0,

      fileId: "", //pdf文件id

    //插件的参数
    Url:api.sourceDataUrl + "/web/Datasource/uploadFile"

    };
  },
  computed: {
    ...mapState(["userInfo", "userUrl", "userName"])
  },
  created() {
    if (!this.userInfo || this.userInfo != "") {
      if (!getStore("htcx_userInfo")) {
        this.$router.push("/login");
      } else {
        this.RECORD_USERINFO(JSON.parse(getStore("htcx_userInfo")));
        this.RECORD_USERURL(getStore("htcx_userUrl"));
        this.RECORD_USERNAME(getStore("htcx_userName"));
      }
    }
    if (this.userUrl == "") {
      this.RECORD_USERURL("../static/images/header_userimg.png");
    }
    this.fileId = this.$route.query.id;
    this.getFileInfo();
    this.getData(1);
  },
  components: {
    reportEditorComment: reportEditorComment,
  },
  mounted() {
    // this.init();
  },
  methods: {
    ...mapMutations(["RECORD_USERINFO", "RECORD_USERURL", "RECORD_USERNAME"]),

    
    getFileInfo() {
      tempInfo({
        id: this.fileId,
        type: this.$route.query.from
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.tinymceHtml = res.data.payload.content;
        } else {
          dialogConfirm("info", res.data.message);
        }
      });
    },

    //通过html获取pdf文件
    getTempPreview(type) {
      if (tinyMCE.editors[0].getContent() == "") {
        dialogConfirm("info", "文书内容不能为空!");
        return false;
      }
      let params = {
        content: tinyMCE.editors[0].getContent()

        // pdf_name:,
      };
      let name = this.$route.query.name;
      name = name.split(".")[0];

      if (this.$route.query.form == 1) {
        params.pdf_name = name;
      } else {
        if (name.lastIndexOf("(") != -1) {
          name = name.substring(0, name.lastIndexOf("("));
        }
        params.pdf_name = name;
      }

      if (this.$route.query.from == 2) {
        params.id = this.fileId;
      }
      if (type == 1) {
        params.type = 1;
      } else {
        params.type = 2;
      }
      // console.log(params);
      // return false;
      tempPreview(params).then(res => {
        if (res.data.msgNo == 1) {
          if (type == 1) {
            this.toShowFilePage(res.data.payload.url);
          } else if (type == 2) {
            location.href = res.data.payload.url;
          } else if (type == 3) {
            dialogConfirm("success", "保存成功");
          }
        } else {
          dialogConfirm("info", res.data.message);
        }
      });
    },
    toShowFilePage(path2) {
      let url = path2;
      const { href } = this.$router.resolve({
        name: "documentView",
        query: {
          filetype: "pdf",
          url: url
        }
      });
      // window.open(href, "_blank");
      let tempwindow = window.open("_blank"); // 先打开页面
      tempwindow.location = href; // 后更改页面地址
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getData(val);
      }
    },
    //退出登录
    logout() {
      let version = getStore("htcx_version");
      window.localStorage.clear();
      window.sessionStorage.clear();
      setStore("htcx_version", version);
      api.resetServer();
      this.$router.push({
        path: "/login"
      });
    },
    //点击页面其他区域隐藏右侧下拉
    headerUserDropboxhide() {
      this.headerUserDropboxShow = false;
    },
    //顶部导航栏下拉框打开关闭
    changeHeaderUserDropboxShow() {
      this.headerUserDropboxShow = !this.headerUserDropboxShow;
    },
    goToHome(name) {
      this.$router.push({
        name: name
      });
    },
    goHome() {
      if (this.firstClickId != null) {
        this.firstClickId = this.firstClickId.toString();
      }
      this.firstClickId = this.firstClickId.toString();
      this.$refs.nav.close(this.firstClickId);
      this.defaultActive = "";
      this.firstClickId = "";
      this.secondClickId = "";
      this.thirdClickId = "";
      this.selectName = "";
      window.sessionStorage.clear();
    },
    getimg() {
      apiAutoLogin().then(res => {
        if (res.data.msgNo == 1) {
          setStore("htcx_userUrl", res.data.payload);
          this.RECORD_USERURL(res.data.payload); //用户信息存全局
          //this.$router.push({ path: '/home/homepage'})
        }
      });
    },
    toInsert(html) {
      tinymce.execCommand("mceInsertContent", false, this.insertHtml);
    },

    toClick() {
      this.isclick = !this.isclick;
    },
    getData(val) {
      this.currentPage = val;
      let params = {
        page: this.currentPage,
        pageSize: this.pageSize
      };
      if (this.keyWord != "") {
        let name = this.keyWord.replace(/\s+/g, "");
        params.search = name;
      }
      getIntelligentDoc(params).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.serarchData = data.list;
          this.total = data.count;
        } else {
          dialogConfirm("info", res.data.message);
        }
      });
    },
    toOpenDoc(html) {
      this.onDeatail = true;
      this.deatailInfo = html;
    },
    toBack() {
      this.onDeatail = false;
    },
    fun(e) {
      let e1 = e || window.event;
      let target = e1.target || e1.srcElement;
      if (target.nodeName.toLowerCase() === "p") {
        this.ishow = true;
        let detail_box = document.getElementById("detail_box");
        let offsetTop = target.offsetTop - detail_box.scrollTop;
        if (offsetTop >= 84) {
          this.insertHtml = target.outerHTML;
          this.clientWidth = target.clientWidth;
          this.clientHeight = target.clientHeight;
          this.offsetTop = offsetTop;
        }
      }
    },
    wheel_event() {
      let detail_box = document.getElementById("detail_box");
      if (detail_box.addEventListener) {
        detail_box.addEventListener(
          "mousewheel",
          MouseWheelHandler,
          function() {
            this.ishow = false;
          }
        );
        detail_box.addEventListener(
          "DOMMouseScroll",
          MouseWheelHandler,
          function() {
            this.ishow = false;
          }
        );
      } else {
        detail_box.attachEvent("onmousewheel", MouseWheelHandler, function() {
          this.ishow = false;
        });
      }
    },
    onEditorUploadComplete(url){
        let tpl = '<img src="%s" />';
        let tplV = tpl.replace('%s',url);
        tinymce.execCommand("mceInsertContent", false, tplV);
        window.tinymce.activeEditor.windowManager.close();
        // dialogConfirm('success','上传成功');
    }
  }
};
</script>

