<template>
    <div>
        <textarea :id="Id" class="editor_container_content"></textarea>
    </div>
</template>

<script>
    import './zh_CN.js'
    import {
        DatasourceUploadFile
    } from "../../config/api";
    import {
        dialogConfirm
    } from '../../config/mUtils.js';
    // import '../../static/tinymce/tinymce.min.js'
    
    export default {
        data() {
            const Id = Date.now()
            return {
                Id: Id,
                Editor: null,
                DefaultConfig: {
                    // GLOBAL
                    height: '100%',
                    theme: 'modern',
                    //   menubar: true,
                    //   toolbar: `styleselect | fontselect | formatselect | fontsizeselect | forecolor backcolor | bold italic underline strikethrough | image  media | table | alignleft aligncenter alignright alignjustify | outdent indent | numlist bullist | preview removeformat  hr | paste code  link | undo redo | fullscreen `,
                    toolbar: " insertfile undo redo | formatselect |  link unlink | fontsizeselect | forecolor backcolor | bold italic underline strikethrough | alignleft aligncenter alignright alignjustify outdent indent |  removeformat | image   | table ", // 工具栏2, mybutton是自己加的自定义按钮
                    plugins: `
                paste
                importcss
                image
                code
                table
                advlist
                fullscreen
                link
                media
                lists
                textcolor
                colorpicker
                hr
                preview
              `,
    
    
                    // CONFIG
    
                    forced_root_block: 'p',
                    force_p_newlines: true,
                    importcss_append: true,
    
                    // CONFIG: ContentStyle 这块很重要， 在最后呈现的页面也要写入这个基本样式保证前后一致， `table`和`img`的问题基本就靠这个来填坑了
                    content_style: `
                            *                         { padding:0; margin:0; }
                            html                      { height:100%;}
                            body                      { height:100%;padding:0px 60px 0px 18px;}
                            img                       { max-width:100%; display:block;height:auto; }
                            a                         { text-decoration: none; }
                            iframe                    { width: 100%;}
                            p                         { line-height:1.6; margin: 0px; padding:0 20px 0 30px !important }
                            table                     { word-wrap:break-word; word-break:break-all; max-width:100%; border:none; border-color:#999; }
                            .mce-object-iframe        { width:100%; box-sizing:border-box; margin:0; padding:0; }
                            ul,ol                     { list-style-position:inside; }
                            .insert_p_lable_box{border: 1px dashed #3C98E8;padding: 10px 20px 10px 29px;cursor: pointer;position: relative;}
                            .insert_p_lable_box img{position: absolute;right: -1px;top:-1px;width:16px;height:16px}
                            .insert_p_lable_box:hover{ border: 1px solid #3C98E8;background-color: #F9FCFF;}
                            .insert_p_lable{padding:0px}`,
    
                    insert_button_items: 'image link | inserttable',
    
                    // CONFIG: Paste
                    paste_retain_style_properties: 'all',
                    paste_word_valid_elements: '*[*]', // word需要它
                    paste_data_images: true, // 粘贴的同时能把内容里的图片自动上传，非常强力的功能
                    paste_convert_word_fake_lists: false, // 插入word文档需要该属性
                    paste_webkit_styles: 'all',
                    paste_merge_formats: true,
                    nonbreaking_force_tab: false,
                    paste_auto_cleanup_on_paste: false,
    
                    // CONFIG: Font
                    fontsize_formats: '10px 11px 12px 14px 16px 18px 20px 24px',
    
                    // CONFIG: StyleSelect
                    style_formats: [{
                            title: '首行缩进',
                            block: 'p',
                            styles: {
                                'text-indent': '2em'
                            }
                        },
                        {
                            title: '行高',
                            items: [{
                                    title: '1',
                                    styles: {
                                        'line-height': '1'
                                    },
                                    inline: 'span'
                                },
                                {
                                    title: '1.5',
                                    styles: {
                                        'line-height': '1.5'
                                    },
                                    inline: 'span'
                                },
                                {
                                    title: '2',
                                    styles: {
                                        'line-height': '2'
                                    },
                                    inline: 'span'
                                },
                                {
                                    title: '2.5',
                                    styles: {
                                        'line-height': '2.5'
                                    },
                                    inline: 'span'
                                },
                                {
                                    title: '3',
                                    styles: {
                                        'line-height': '3'
                                    },
                                    inline: 'span'
                                }
                            ]
                        }
                    ],
    
                    // FontSelect
                    font_formats: `
                微软雅黑=微软雅黑;
                宋体=宋体;
                黑体=黑体;
                仿宋=仿宋;
                楷体=楷体;
                隶书=隶书;
                幼圆=幼圆;
                Andale Mono=andale mono,times;
                Arial=arial, helvetica,
                sans-serif;
                Arial Black=arial black, avant garde;
                Book Antiqua=book antiqua,palatino;
                Comic Sans MS=comic sans ms,sans-serif;
                Courier New=courier new,courier;
                Georgia=georgia,palatino;
                Helvetica=helvetica;
                Impact=impact,chicago;
                Symbol=symbol;
                Tahoma=tahoma,arial,helvetica,sans-serif;
                Terminal=terminal,monaco;
                Times New Roman=times new roman,times;
                Trebuchet MS=trebuchet ms,geneva;
                Verdana=verdana,geneva;
                Webdings=webdings;
                Wingdings=wingdings,zapf dingbats`,
    
                    // Tab
                    tabfocus_elements: ':prev,:next',
                    object_resizing: true,
    
                    // Image
                    imagetools_toolbar: 'rotateleft rotateright | flipv fliph | editimage imageoptions'
                }
            }
        },
        props: {
            value: {
                default: '',
                type: String
            },
            config: {
                type: Object,
                default: () => {
                    return {
                        theme: 'modern',
                        height: 300
                    }
                }
            },
            url: {
                default: '',
                type: String
            },
            accept: {
                default: 'image/jpeg, image/png' ,
                type: String
            },
            maxSize: {
                default: 2097152,
                type: Number
            },
            withCredentials: {
                default: false,
                type: Boolean
            }
        },
        mounted() {
            this.init()
        },
        beforeDestroy() {
            // 销毁tinymce
            this.$emit('on-destroy')
            window.tinymce.remove('#' + this.Id)
        },
        methods: {
            init() {
                const self = this
    
                this.Editor = window.tinymce.init({
                    // 默认配置
                    ...this.DefaultConfig,
    
                    // 图片上传
                    images_upload_handler: function(blobInfo, success, failure) {
                        // console.log(self.accept.indexOf('1111'));
                        if (blobInfo.blob().size > self.maxSize) {
                            failure('文件体积过大')
                        }

                        if (self.accept.indexOf(blobInfo.blob().type) != -1) {
                            let _this = self;
                            DatasourceUploadFile(blobInfo.blob()).then(res => {
                                self.$emit('on-upload-success', res.data.payload.url)
                            })
                        } else {
                            failure('图片格式错误')
                        }
                    },
    
                    // prop内传入的的config
                    ...this.config,
    
                    // 挂载的DOM对象
                    selector: '#' + this.Id,
                    setup: (editor) => {
                        // 抛出 'on-ready' 事件钩子
                        editor.on(
                            'init', () => {
                                self.loading = false
                                self.$emit('insert')
                                editor.setContent(self.value)
                            }
                        )
                        // 抛出 'input' 事件钩子，同步value数据
                        editor.on(
                            'input change undo redo', () => {
                                self.$emit('input', editor.getContent())
                            }
                        )
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
.editor_container_content{
    height: 100%;
}

</style>



